/*-------------------------- TOOLBAR ACTIONS -------------------------*/
/*--------------------------------------------------------------------*/

.medium-editor-toolbar ul {
    text-align: center;

    position: relative;
    margin: 0;

    min-width: 200px;
    max-width: 320px;
    height: 43px;
    padding: 0;

    background-color: var(--color-button-bg);
