/*--------------------------------------------------------------------*/
/*------------------------------ BUTTONS -----------------------------*/
/*--------------------------------------------------------------------*/

.E_PageMenu--button {
    z-index: 2;
    position: absolute;
    top: 0px;
    left: 0px;

    width: 64px;
    height: 64px;

