/*--------------------------------------------------------------------*/
/*------------------------------ CONTENT -----------------------------*/
/*--------------------------------------------------------------------*/

.E_PageMenu--content {
    position: relative;

    width: 100%;
    height: 100%;

    background-color: #fcfcfc;
    opacity: 0;
    visibility: hidden;

    transition:
        opacity    0.3s linear 0s,
        visibility 0s   linear 0.3s;

    overflow: hidden;
}

.E_PageMenu--item__show-content .E_PageMenu--content {
