/*---------------------------- RESIZER ----------------------------*/

.E_PageMenu--resizer {
    z-index: 100;
    position: absolute;
    top: 0px;
    left: calc(100% - 2px);

    width: 10px;
    height: 100%;
    min-height: 100%;

    cursor: ew-resize;

