/*---------------------------- FLATBUTTON ----------------------------*/
/*====================================================================*/

.FlatButton--icon svg,
.FlatButton--text,
.FlatButton {
    transition: var(--default-transition);
}

.FlatButton:hover .FlatButton--icon svg,
.FlatButton:hover .FlatButton--text,
.FlatButton:hover {
    transition: var(--default-transition-hover);
}

.FlatButton:active .FlatButton--icon svg,
.FlatButton:active .FlatButton--text,
.FlatButton:active {
    transition: var(--default-transition-active);
}

.FlatButton {
    font-size: 0;
    line-height: 0;

    position: relative;

    box-sizing: border-box;

    border: none;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    overflow: hidden;

    touch-action: manipulation;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}

.FlatButton--event {
    z-index: 2;
    position: absolute;
    top: 0px;
    left: 0px;

    width: 100%;
    height: 100%;
}

.FlatButton--event ~ * {
    pointer-events: none;
}

.FlatButton--icon {
    font-size: 0;
    line-height: 0;

    position: relative;
    margin: 0 auto;
}

.FlatButton--icon svg {
    width: 100%;
    height: 100%;
}

.FlatButton--wrapper {
    font-size: 0;
    line-height: 0;

    position: relative;

    border-radius: 2px;

    overflow: hidden;
}

.FlatButton--text {
    font-family: "Encode Sans Condensed", sans-serif;
    font-weight: 600;
    text-align: center;
    line-height: 1;
    white-space: nowrap;

    position: relative;

    min-width: 100%;

    display: inline-block;

    will-change: transform;
}

.FlatButton--text__long,
.FlatButton--text__extra-long {
    font-family: "Encode Sans Compressed";
}

