.P_PageElement__button {
    font-size: 0;
    line-height: 0;

    position: relative;
    margin: 0 8px;
    margin-bottom: 16px;

    display: inline-block;
}

.E.P_Page__loaded .P_PageElement__button[data-ractive-transition="intro"] {
    animation: P_PageElement__button-show 0.3s ease-in-out forwards;
}

.E.P_Page__loaded .P_PageElement__button[data-ractive-transition^="outro"] {
    animation: P_PageElement__button-hide 0.3s ease-in-out forwards;
}

@keyframes P_PageElement__button-show {
    from {
        margin: 0;
    }
    to {
        margin: 0 8px;
        margin-bottom: 16px;
    }
}

@keyframes P_PageElement__button-hide {
    from {
        margin: 0 8px;
        margin-bottom: 16px;
    }
    to {
        margin: 0;
    }
}

@keyframes P_PageElement__button-show__480 {
    from {
        margin: 0;
    }
    to {
        margin: 0 6px;
        margin-bottom: 12px;
    }
}

@keyframes P_PageElement__button-hide__480 {
    from {
        margin: 0 6px;
        margin-bottom: 12px;
    }
    to {
        margin: 0;
    }
}

@keyframes P_PageElement__button-show__360 {
    from {
        margin: 0;
    }
    to {
        margin-bottom: 8px;
    }
}

@keyframes P_PageElement__button-hide__360 {
    from {
        margin-bottom: 8px;
    }
    to {
        margin: 0;
    }
}

.P_PageElementButton {
    position: relative;
    margin: 0;

    height: 56px;
    max-width: 300px;

    border: 2px solid;

    display: inline-block;
    overflow: hidden;

    transition:
        color 0.1s linear 0s,
        background 0.1s linear 0s,
        border 0.1s linear 0s,
        border-radius 0.1s linear 0s,
        box-shadow 0.1s linear 0s,
        text-shadow 0.1s linear 0s;
}

.E_PageSection__stop-color-transitions .P_PageElementButton {
    transition:
        box-shadow 0.1s linear 0s,
        border-radius 0.1s linear 0s,
        text-shadow 0.1s linear 0s;
}

.P_PageElement:not(.E_PageElement) .P_PageElementButton:focus,
.P_PageElement:not(.E_PageElement) .P_PageElementButton:hover,
body.E_actions-active .P_PageElement .P_PageElementButton:focus,
body.E_actions-active .P_PageElement .P_PageElementButton:hover {
    transform: scale(1.07142);
}

.P_PageElement:not(.E_PageElement) .P_PageElementButton:active,
body.E_actions-active .P_PageElement.E_PageElement .P_PageElementButton:active {
    transform: scale(1.03571);
}

@media (max-width: 480px) {

    .P_PageElement__button {
        margin: 0 6px;
        margin-bottom: 12px;
    }

    .E.P_Page__loaded .P_PageElement__button[data-ractive-transition="intro"] {
        animation-name: P_PageElement__button-show__480;
