@keyframes P_PageElementButton--width-auto {
    from { width: 100%; }
    to { width: auto; }
}

.P_PageElementButton--content-wrapper {
    vertical-align: middle;
    white-space: nowrap;

    height: 52px;
    min-height: 52px;
    max-height: 52px;
    max-width: 296px;

    display: inline-table;
    table-layout: fixed;

    overflow: hidden;

    transition: padding 0.3s ease-in-out 0s,
        width 0.3s ease-in-out 0s;
}

.P_PageElementButton--text-wrapper {
    font-size: 19px;
    line-height: 52px;
    text-align: center;

    padding: 0;
    max-height: 52px;

    display: table-cell;
}

.P_PageElementButton--text {
    line-height: 22px;
    font-weight: bold;
    white-space: normal;
    word-break: break-all;
    word-wrap: break-word;
    vertical-align: middle;
    text-align: center;
    text-wrap: balance;

    top: 1px;
    position: relative;

    padding: 4px 20px;
    min-width: 96px;
    max-height: 52px;
    height: 52px;

    display: table-cell;

    -webkit-user-select: text;
    -moz-user-select: text;
    -ms-user-select: text;
    -o-user-select: text;
    user-select: text;

    transition: padding 0.3s ease-in-out 0s,
        color 0.1s linear 0s,
        text-shadow 0.1s linear 0s;
}

.E_PageSection__stop-color-transitions .P_PageElementButton--text {
    transition: padding 0.3s ease-in-out 0s;
}

.P_PageElementButton__long-text .P_PageElementButton--text-wrapper {
    font-size: 17px;
}

.P_PageElementButton__long-text .P_PageElementButton--text {
    top: 0px;
}

.P_PageElementButton--icon {
    line-height: 0;
    vertical-align: middle;

    position: relative;

    width: 56px;
    height: 32px;
    max-height: 52px;

    -webkit-background-size: 100%;
    background-size: 100%;

    display: table-cell;
    overflow: hidden;

    transition: width 0.3s ease-in-out;
}

.P_PageElementButton__has-icon .P_PageElementButton--text {
    padding-left: 0;
}

.P_PageElementButton--icon svg {
    margin: auto;
    position: absolute;
    top: 10px;
    left: 0px;
    right: 0;

    width: 32px;
    height: 32px;

    fill: currentColor;

    display: inline-block;
    transition:
        color 0.1s linear 0s,
        fill 0.1s linear 0s,
        left 0.3s ease-in-out;
}

.E_PageSection__stop-color-transitions .P_PageElementButton--icon svg {
    transition: none;
}

.E.P_Page__loaded .P_PageElementButton--icon[data-ractive-transition="intro"] {
    animation: P_PageElementButton--icon-width-0-56 0.3s ease-in-out forwards;
}

.E.P_Page__loaded .P_PageElementButton--icon[data-ractive-transition^="outro"] {
    animation: P_PageElementButton--icon-width-56-0 0.3s ease-in-out forwards;
}

@keyframes P_PageElementButton--icon-width-0-56 {
    from { width: 0; }
    to { width: 56px; }
}

@keyframes P_PageElementButton--icon-width-56-0 {
    from { width: 56px; }
    to { width: 0px; }
}

