/*----------------------------- DROPDOWN -----------------------------*/
/*--------------------------------------------------------------------*/

.P_PageElementMenu--dropdown {
    position: relative;

    cursor: default;
}

.P_PageElementMenu--dropdown-selected {
    position: relative;

    padding-right: 14px;

    color: inherit;
}

.P_PageElementMenu--dropdown-icon {
    margin: auto;
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0;

    width: 8px;
    height: 8px;

    color: inherit;

    transform: rotate(-90deg);
    transition-property: transform;
}

.P_PageElementMenu--dropdown.open .P_PageElementMenu--dropdown-icon {
    transform: rotate(-180deg);
}

.P_PageElementMenu--dropdown-icon svg {
    position:absolute;
    top: 0px;
    left: 0px;

    width: 100%;
    height: 100%;

    fill: currentColor;
}

