    line-height: 0;
    
    margin: 0;
    z-index: 149;
    position: fixed;
    top: 0px;
    right: 0px;
    
    width: 40px;
    height: 40px;
    min-width: 40px;
    max-width: 40px;
    
    opacity: 0;
    visibility: hidden;
    cursor: pointer;
    
    transform: rotate(90deg);
    
    display: none;
}

.P_PageElementMenu--dropdown-close__active {
    opacity: 1;
    visibility: visible;
    
    transform: rotate(0deg);
}

.P_PageElementMenu--dropdown-close svg {
    margin: auto;
    position: absolute;
    top: 0px;
    right: 0px;
    left: 0;
    bottom: 0;
    
