    top: 100%;
    right: 0px;    

    width: max-content;
    min-width: 100%;
    max-width: 320px;
    
    backdrop-filter: blur(0px);
    opacity: 0;
    visibility: hidden;

    transform: translateY(-14px);
    clip-path: inset(0px 0px 0px 0px);

    display: inline-block;

    will-change: transform, backdrop-filter;
}

.P_PageElementMenu--dropdown-list-bg {
    z-index: -1;
