/*--------------------------------------------------------------------*/
/*---------------------------- COLOR BOXES ---------------------------*/
/*--------------------------------------------------------------------*/

.ColorPicker--widget-wrapper {
    margin-bottom: 8px;
    margin-right: 24px;

    flex-basis: 220px;
    flex-shrink: 1;
    flex-grow: 0;

    display: flex;
    flex-wrap: wrap;
}

.ColorPicker--HSV-wrapper {
    font-size: 0;

    width: 100%;
}

/*---------------------------- BOXES ----------------------------*/

.ColorPicker--SV-box {
    vertical-align: top;

    position: relative;
    margin-bottom: 8px;

    width: calc(100% - 12px - 12px);
    height: 0px;
    padding-bottom: calc(100% - 8px - 14px - 1px - 1px);

    background-image:
        linear-gradient(
            to bottom,
            rgba(0, 0, 0, 0),
            rgba(0, 0, 0, 1)
        ),
        linear-gradient(
            to right,
            rgb(255, 255, 255),
            rgb(255,   0,   0)
        );
    border-radius: 4px;
    box-shadow:
        inset 1px 1px 1px rgba(34, 34, 34, 0.1),
        inset 0px 0px 2px rgba(34, 34, 34, 0.55),
        inset 2px 2px 4px rgba(34, 34, 34, 0.15);

    display: inline-block;
    will-change: background-image, box-shadow;
}

.ColorPicker--H-box {
    position: relative;
    margin-left: 12px;

    width: 12px;
    min-width: 12px;
    height: 0;
    padding-bottom: calc(100% - 8px - 14px - 1px - 1px);

    background-image:
        linear-gradient(
            to top,
            #ff0000   0.00%,
            #ffff00  16.67%,
            #00ff00  33.33%,
            #00ffff  50.00%,
            #0000ff  66.67%,
            #ff00ff  83.33%,
            #ff0000 100.00%
        );
    border-radius: 4px;
    box-shadow:
        inset 1px 1px 1px rgba(34, 34, 34, 0.1),
        inset 0px 0px 2px rgba(34, 34, 34, 0.55),
        inset 2px 2px 4px rgba(34, 34, 34, 0.15);

    display: inline-block;
    will-change: box-shadow;
}

.ColorPicker--H-box:before,
.ColorPicker--H-box:after {
    content: "";

    position: absolute;
    top: -1px;

    width: 9px;
    height: calc(100% + 2px);
}

.ColorPicker--H-box:before {
    left: -9px;
}

.ColorPicker--H-box:after {
    left: 12px;
}

/*---------------------------- SELECTORS ----------------------------*/

.ColorPicker--SV-selector,
.ColorPicker--H-selector {
    position: absolute;

    will-change: transform, box-shadow;
    transition: transform 0.35s cubic-bezier(.1, 1, .4, 1) 0s,
        background-color 0.35s cubic-bezier(.1, 1, .4, 1) 0s;
}

.ColorPicker--SV-selector {
    top: -8px;
    left: -8px;
    transform: translate(0px, 0px);

    width: 16px;
    height: 16px;

    border-radius: 50%;
