/*====================================================================*/
/*------------------------------ DIALOG ------------------------------*/
/*====================================================================*/

.Dialog--overlay {
    z-index: 99999;
    position: fixed;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    background-color: rgba(0, 0, 0, 0.65);

    will-change: opacity;
}

.Dialog--box {
    position: absolute;
    top: 50%;
    left: 50%;

    min-width: 300px;
    max-width: 480px;
    padding: 24px 32px;

    background-color: #fcfcfc;
    border: 2px solid #888;
    border-radius: 4px;
    box-shadow: 0px 0px 32px rgba(0, 0, 0, 0.35);

    transform: translate(-50%, -50%) scale(0);

    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    will-change: transform, opacity;
}

.Dialog--box__success {
    border-color: var(--color-ok);
}

.Dialog--box__error {
    border-color: var(--color-danger);
}

.Dialog--box__warn {
    border-color: var(--color-warn);
}

.Dialog--box__info {
    border-color: var(--color-temp-selected-light);
}

.Dialog--box[data-ractive-transition="intro"] {
    animation: Dialog--toggle 0.2s linear 0.3s forwards;
}

.Dialog--box[data-ractive-transition="intro-complete"] {
    transform: translate(-50%, -50%) scale(1);
}

.Dialog--box[data-ractive-transition="outro"] {
    animation: Dialog--toggle 0.2s linear 0s reverse;
}

.Dialog--box[data-ractive-transition="outro-complete"] {
    transform: translate(-50%, -50%) scale(0);
}

@keyframes Dialog--toggle {
    from {
        transform: translate(-50%, -50%) scale(0);

        opacity: 0;
    }
    to {
        transform: translate(-50%, -50%) scale(1);

        opacity: 1;
    }
}

.Dialog--title {
    font-family: "Encode Sans", sans-serif;
    font-size: 21px;
    font-weight: 600;
    line-height: 1.19;
    text-align: center;

    margin-top: 4px;
    margin-bottom: 12px;

    width: 100%;

    color: var(--color-text-1);
}

.Dialog--text {
    font-family: "Encode Sans", sans-serif;
    font-size: 14px;
    line-height: 1.64;
    text-align: center;

    margin-bottom: 16px;

    width: 100%;

    color: var(--color-text-2);
}

.Dialog--text:last-child {
    margin-bottom: 4px;
}

.Dialog--close {
    position:absolute;
    top: 0px;
    right: 0px;

    width: 24px;
    height: 24px;

    display: flex;
    justify-content: center;
    align-items: center;
}

.Dialog .Button {
    margin: 0 12px;
}

.Dialog--close svg {
    width: 12px;
    height: 12px;

    fill: var(--color-text-1);
    opacity: 0.5;

    transition: var(--default-transition);
}

.Dialog--close:focus svg,
.Dialog--close:hover svg {
    opacity: 1;

    transition: var(--default-transition-hover);
}

.Dialog--close:active svg {
    transform: scale(0.9);

    transition: var(--default-transition-active);
}

@media (pointer: coarse) {

    .Dialog--close {
        width: 32px;
        height: 32px;
    }

    .Dialog--close svg {
        width: 16px;
        height: 16px;
    }
}

