    width: 100%;
    padding: 0 12px;
    padding-right: 56px;
    padding-left: 50px;

    color: inherit;
    background-color: #f5f5f5;
    border: none;
    border-radius: 0;

    transition: var(--default-transition);

    -webkit-user-select: auto;
    -moz-user-select: auto;
    -ms-user-select: auto;
    -o-user-select: auto;
    user-select: auto;
}

.FileBrowser--search-input:hover {
    background-color: #fff;

    transition: var(--default-transition-hover);
}

.FileBrowser--directory__opened .FileBrowser--search-input {
    color: #fff;
    background-color: var(--color-temp-selected);

    transition: var(--default-transition-hover);
}

.FileBrowser--search-input::-webkit-input-placeholder { color: rgba(64, 64, 64, 0.65); }
.FileBrowser--search-input::-moz-placeholder { color: rgba(64, 64, 64, 0.65); opacity: 1; }
.FileBrowser--search-input:-ms-input-placeholder { color: rgba(64, 64, 64, 0.65); }
.FileBrowser--search-input:-moz-placeholder { color: rgba(64, 64, 64, 0.65);  opacity: 1; }
.FileBrowser--search-input:placeholder { color: rgba(64, 64, 64, 0.65); opacity: 1; }

.FileBrowser--directory__opened .FileBrowser--search-input::-webkit-input-placeholder { color: rgba(255, 255, 255, 0.75); }
.FileBrowser--directory__opened .FileBrowser--search-input::-moz-placeholder { color: rgba(255, 255, 255, 0.75); opacity: 1; }
.FileBrowser--directory__opened .FileBrowser--search-input:-ms-input-placeholder { color: rgba(255, 255, 255, 0.75); }
.FileBrowser--directory__opened .FileBrowser--search-input:-moz-placeholder { color: rgba(255, 255, 255, 0.75);  opacity: 1; }
.FileBrowser--directory__opened .FileBrowser--search-input:placeholder { color: rgba(255, 255, 255, 0.75); opacity: 1; }

.FileBrowser--search .FileBrowser--icon {
    z-index: 5;

    position:absolute;
    top: 0px;
    left: 0px;
}

.FileBrowser--directory:not(.FileBrowser--directory__opened):hover .FileBrowser--search .FileBrowser--icon svg {
    fill: var(--color-text-2);
}

/*--------------------------------------------------------------------*/
/*------------------------------- FILES ------------------------------*/
/*--------------------------------------------------------------------*/

.FileBrowser--files {
    position: relative;

    padding: 8px;

    color: var(--color-text-2);
    background-color: #f2f2f2;

    display: flex;
    flex-flow: row wrap;
    align-items: center;

    overflow: hidden;
}

.FileBrowser--directory:last-of-type .FileBrowser--files {
    border-bottom: 1px solid #d7d7d7;
}

.FileBrowser--files:after {
    content: "";

    position: absolute;
    top: 0px;
    left: 0px;

    width: 100%;
    height: 100%;

    box-shadow: inset 0px 0px 8px 0px rgba(0, 0, 0, 0.10);

    pointer-events: none;
}

.FileBrowser--file {
    font-size: 0;
    text-align: center;

    position: relative;

    will-change: transform, opacity, box-shadow;
    transition: var(--default-transition);
}

.FileBrowser--file-wrapper {
    font-size: 0;

    display: inline-block;

    overflow: hidden;

    will-change: transform, opacity, box-shadow;
    transition: var(--default-transition);
}

.FileBrowser--file-wrapper:hover {
    transition: var(--default-transition-hover);
}

.FileBrowser--file-wrapper:active {
    transition: var(--default-transition-active);
}

/*---------------------------- FILE-IMAGE ----------------------------*/

.FileBrowser__images .FileBrowser--files:not(.FileBrowser--files__init-dir-content) .FileBrowser--file[data-ractive-transition="intro"] {
    animation:
        FileBrowser--file-intro__image 0.45s linear forwards 0s;
}

@keyframes FileBrowser--file-intro__image {
    0% {
        margin-top: -112px;
        margin-right:-112px;

        opacity: 0;
    }
    50% {
        margin-top: 0px;
        margin-right:0px;

        opacity: 0;
    }
    100% {
        margin-top: 0px;
        margin-right:0px;

        opacity: 1;
    }
}

.FileBrowser__images .FileBrowser--file[data-ractive-transition="outro"] {
    animation:
        FileBrowser--file-outro__image 0.45s linear 0s forwards;
}

@keyframes FileBrowser--file-outro__image {
    0% {
        margin-top: 0;
        margin-right:0;

        opacity: 1;
    }
    50% {
        margin-top: 0px;
        margin-right: 0px;

        opacity: 0;
    }
    100% {
        margin-top: -112px;
        margin-right:-112px;

        opacity: 0;
    }
}

.FileBrowser--file__image .FileBrowser--file-wrapper {
    transform: scale(1);

    transition-duration: 0.2s;
}

.FileBrowser--file__image .FileBrowser--file-wrapper:hover {
    transform: scale(1.0625);

    transition: var(--default-transition-hover);
    transition-delay: 0.1s;
    transition-duration: 0.075s;
}

.FileBrowser--file__image .FileBrowser--file-wrapper:active {
    transform: scale(1.0416666);

    transition: var(--default-transition-active);
    transition-delay: 0s;
    transition-duration: 0.05s;
}

@media (pointer: coarse) {

    .FileBrowser--file__image .FileBrowser--file-wrapper:hover {
        transform: scale(1.0416666);
    }

    .FileBrowser--file__image .FileBrowser--file-wrapper:active {
        transform: scale(0.97916666);
    }

}

.FileBrowser--file__image {
    width: 112px;
    height: 112px;
    padding: 8px;
}

.FileBrowser--file__image .FileBrowser--file-wrapper {
    line-height: 96px;

    position: relative;

    width: 96px;
    height: 96px;

    background-color: #fff;
    background: 
        linear-gradient(45deg, rgba(0,0,0,0.1) 25%, rgba(0,0,0,0.015) 25%, rgba(0,0,0,0.015) 75%, rgba(0,0,0,0.1) 75%, rgba(0,0,0,0.1) 0),
        linear-gradient(45deg, rgba(0,0,0,0.1) 25%, rgba(0,0,0,0.015) 25%, rgba(0,0,0,0.015) 75%, rgba(0,0,0,0.1) 75%, rgba(0,0,0,0.1) 0);
    background-position: 0 0, 8px 8px;
    background-size: 16px 16px;
    box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.12);
}

.FileBrowser--file__image .FileBrowser--file-wrapper:before {
    content: "";

    position: absolute;
    top: 0px;
    left: 0px;

    width: 100%;
    height: 100%;

    box-shadow: inset 0px 0px 0px 1px rgba(0, 0, 0, 0.15);

    transition: inherit;
}

.FileBrowser--file__image.FileBrowser--file__selected .FileBrowser--file-wrapper:before {
    box-shadow: inset 0px 0px 0px 1px var(--color-selected-85);
}

.FileBrowser--file__error.FileBrowser--file__image img,
.FileBrowser--file__uploading.FileBrowser--file__image img {
    opacity: 0.5;

    transition: var(--default-transition-hover);
}

.FileBrowser--file__error.FileBrowser--file__image .FileBrowser--file-wrapper {
    opacity: 0.5;
}

.FileBrowser--file__image .FileBrowser--file-wrapper:after {
    content: "";

    position: absolute;
    top: 0px;
    left: 0px;

    width: 100%;
    height: 100%;

    box-shadow: inset 0px 0px 1px 1px var(--color-danger-half);
    opacity: 0;

    transition: var(--default-transition);
}

.FileBrowser--file__error.FileBrowser--file__image .FileBrowser--file-wrapper:after {
    opacity: 1;
}

.FileBrowser--file__image img {
    vertical-align: middle;

    max-width: 100%;
    max-height: 100%;

    transition: var(--default-transition);
}

/*---------------------------- FILE-ICON ----------------------------*/

.FileBrowser__icon .FileBrowser--files:not(.FileBrowser--files__init-dir-content) .FileBrowser--file[data-ractive-transition="intro"] {
    animation:
        FileBrowser--file-intro__icon 0.45s linear forwards 0s;
}

@keyframes FileBrowser--file-intro__icon {
    0% {
        margin-top: -64px;
        margin-right:-64px;

        opacity: 0;
    }
    50% {
        margin-top: 0px;
        margin-right:0px;

        opacity: 0;
    }
    100% {
        margin-top: 0px;
        margin-right:0px;

        opacity: 1;
    }
}

.FileBrowser__icon .FileBrowser--file[data-ractive-transition="outro"] {
    animation:
        FileBrowser--file-outro__icon 0.45s linear 0s forwards;
}

@keyframes FileBrowser--file-outro__icon {
    0% {
        margin-top: 0;
        margin-right:0;

        opacity: 1;
    }
    50% {
        margin-top: 0px;
        margin-right: 0px;

        opacity: 0;
    }
    100% {
        margin-top: -64px;
        margin-right:-64px;

        opacity: 0;
    }
}

.FileBrowser--file__icon {
    padding: 12px;

    transform: translateZ(0);
}

.FileBrowser--file__icon .FileBrowser--file-wrapper {
    line-height: 40px;

    position: relative;

    width: 40px;
    height: 40px;

    overflow: visible;
}

.FileBrowser--file__icon .FileBrowser--file-wrapper img,
.FileBrowser--file__icon .FileBrowser--file-wrapper svg {
    vertical-align: middle;

    max-width: 100%;
    max-height: 100%;

    fill: var(--color-text-1);
    stroke: var(--color-text-1);

    transform: translateZ(0);

    transition: var(--default-transition);
}

.FileBrowser--file__icon.FileBrowser--file__selected:hover .FileBrowser--file-wrapper svg,
.FileBrowser--file__icon:hover .FileBrowser--file-wrapper svg {
    fill: var(--color-selected);
    stroke: var(--color-selected);

    transition: var(--default-transition-hover);
}

.FileBrowser--file__icon .FileBrowser--file-wrapper:before {
    content: "";

    position: absolute;
    top: 0px;
    left: 0px;

    width: 100%;
    height: 100%;

    box-shadow: 0px 0px 0px 1px rgba(255, 255, 255, 0);

    transition: var(--default-transition);
}

.FileBrowser--file__icon.FileBrowser--file__selected:not(.FileBrowser--file__svg):hover .FileBrowser--file-wrapper:before,
.FileBrowser--file__icon:not(.FileBrowser--file__svg):hover .FileBrowser--file-wrapper:before {
    box-shadow: 0px 0px 0px 1px var(--color-selected);

    transition: var(--default-transition-hover);
}

.FileBrowser--file__icon.FileBrowser--file__error .FileBrowser--file-wrapper:before {
    box-shadow: 0px 0px 0px 1px var(--color-danger);

    transition: var(--default-transition-hover);
}

/*--------------------------------------------------------------------*/
/*------------------------------ DELETE ------------------------------*/
/*--------------------------------------------------------------------*/

.FileBrowser--delete-file-confirm,
.FileBrowser--delete-file {
    z-index: 5;
    position: absolute;
    top: 12px;
    right: 12px;

    width: 16px;
    height: 16px;

    color: #fff;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 50%;
    box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.15);

    display: flex;
    align-items: center;
    justify-content: center;

    cursor: default;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;

    transition: var(--default-transition);
}

.FileBrowser--delete-file-confirm {
    background-color: var(--color-ok);
}

.FileBrowser--delete-file-confirm:before,
.FileBrowser--delete-file:before {
    content: "";

    z-index: -1;
    position: absolute;
    top: -4px;
    left: -4px;

    width: calc(100% + 8px);
}

.FileBrowser--delete-file-confirm:hover,
.FileBrowser--delete-file:hover {
    transition: var(--default-transition-hover);
}

.FileBrowser--delete-file:hover {
    background-color: var(--color-danger);
}

.FileBrowser--delete-file-confirm:active,
.FileBrowser--delete-file:active {
    transform: scale(0.95);

    transition: var(--default-transition-active);
}

.FileBrowser--delete-file-confirm + .FileBrowser--delete-file {
    z-index: 10;
    right: 32px;

    background-color: var(--color-danger);
}

.FileBrowser--delete-file-confirm svg,
.FileBrowser--delete-file svg {
    width: 8px;
    height: 8px;

    fill: currentColor;

    transform-origin: center center;

    transition: var(--default-transition);
}

.FileBrowser--delete-file-confirm:hover svg,
.FileBrowser--delete-file:hover svg {
    transition: var(--default-transition-hover);
}

.FileBrowser--delete-file path {
    transform-origin: center center;

    transition: var(--default-transition);
}

.FileBrowser--delete-file-confirm + .FileBrowser--delete-file path + path {
    transform-origin: center center;
    transform: rotate(90deg);
}

.FileBrowser--delete-file:hover path {
    transition: var(--default-transition-hover);
}

.FileBrowser--delete-file-confirm + .FileBrowser--delete-file svg {
    transform: rotate(45deg);
}

@media (pointer: coarse) {

    .FileBrowser--delete-file-confirm,
    .FileBrowser--delete-file {
        top: 14px;
        right: 14px;

        width: 20px;
        height: 20px;
    }

    .FileBrowser__icon .FileBrowser--delete-file-confirm,
    .FileBrowser__icon .FileBrowser--delete-file {
        top: 2px;
        right: 2px;
    }

    .FileBrowser--delete-file-confirm:before,
    .FileBrowser--delete-file:before {
        top: -6px;
        left: -6px;

        width: calc(100% + 12px);
        height: calc(100% + 12px);
    }

    .FileBrowser__icon .FileBrowser--delete-file-confirm:before,
    .FileBrowser__icon .FileBrowser--delete-file:before {
        top: -2px;
        left: -2px;
    }

    .FileBrowser--delete-file-confirm + .FileBrowser--delete-file {
        right: 40px;
    }

    .FileBrowser__icon .FileBrowser--delete-file-confirm + .FileBrowser--delete-file {
        right: 28px;
    }

    .FileBrowser--delete-file-confirm svg,
    .FileBrowser--delete-file svg {
        width: 10px;
        height: 10px;
    }
}

/*--------------------------------------------------------------------*/
/*------------------------------ LOADER ------------------------------*/
/*--------------------------------------------------------------------*/

.FileBrowser--loader:before,
.FileBrowser--loader:after,
.FileBrowser--loader {
    width: 8px;
    height: 8px;

    border-radius: 50%;

    will-change: box-shadow;

    animation: FileBrowser--loader 1.4s ease-out both infinite;
}

.FileBrowser--loader {
    font-size: 0;
    text-indent: -99999px;

    vertical-align: middle;
    margin-left: 8px;
    position: relative;
    top: -8px;

    color: currentColor;

    display: inline-block;

    transform: translateZ(0);
    animation-delay: -0.28s;
}

.FileBrowser--loader__uploading {
    margin: auto;
    z-index: 100;
    position:absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;

    transform: translateZ(0) translateX(-14px) translateY(-8px);
}

.FileBrowser--loader__searching {
    margin: auto;
    z-index: 100;
    position:absolute;
    top: 0px;
    bottom: 0px;
    right: 12px;

    color: #fff;

    transform: translateZ(0) translateX(-28px) translateY(-8px);
}

.FileBrowser--loader__directories {
    margin: auto;
    z-index: 100;
    position:absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;

    transform: translateZ(0) translateX(-14px) translateY(-8px);
}

.FileBrowser--loader:before {
    left: 14px;

    animation-delay: -0.14s;
}

.FileBrowser--loader:after {
    left: 28px;
}

.FileBrowser--loader:before,
.FileBrowser--loader:after {
    content: "";

    position: absolute;
    top: 0;
}

@keyframes FileBrowser--loader {
    0%,
    80%,
    100% {
        box-shadow: 0 8px 0 -4px;
    }
    40% {
        box-shadow: 0 8px 0 0;
    }
}

/*====================================================================*/
/*--------------------------- InlineWidget ---------------------------*/
/*====================================================================*/

.InlineWidget {
    line-height: 1;

    position: relative;
}

.InlineWidget__ {
    width: 100%;

    background-color: #f7f7f7;
    border-top: 1px solid #d7d7d7;
    border-bottom: 1px solid #d7d7d7;
}

.InlineWidget--shadow {
    content: "";

    z-index: 500;
    position: absolute;
    top: 0px;
    left: -8px;

    width: calc(100% + 16px);
    height: 100%;

    box-shadow: inset 0px 0px 8px 0px rgba(0, 0, 0, 0.3);

    pointer-events: none;
}

/*====================================================================*/
/*---------------------------- PROGRESSBAR ---------------------------*/
/*====================================================================*/

.E_ProgressBar {
    z-index: 400;
    position: absolute;
    top: 0px;
    left: 0px;

    width: 100%;

    opacity: 0;

    will-change: opacity;
    transition:
        opacity var(--default-transition-duration) var(--default-transition-easing) 3s,
        background-color var(--default-transition-duration) var(--default-transition-easing) 0s,
        box-shadow var(--default-transition-duration) var(--default-transition-easing) 0s;
}

.E_ProgressBar__active {
    opacity: 1;

    transition: var(--default-transition-hover);
}

.E_ProgressBar .E_ProgressBar--progress {
    position:absolute;
    top: 0px;
    left: 0px;

    height: 1px;
    width: 0%;

    background-color: var(--color-temp-selected-light-over);
    box-shadow: 0px 0px 4px 1px var(--color-temp-selected-light-over);

    will-change: width, box-shadow;
    transition: var(--default-transition);
    transition-timing-function: linear;
}

.E_ProgressBar__success .E_ProgressBar--progress {
    background-color: var(--color-ok-over);
    box-shadow: 0px 0px 4px 1px var(--color-ok-over);
}

.E_ProgressBar__error .E_ProgressBar--progress {
    background-color: var(--color-danger-over);
    box-shadow: 0px 0px 4px 1px var(--color-danger-over);
}

.E_ProgressBar__warn .E_ProgressBar--progress,
.E_ProgressBar__error.E_ProgressBar__warn .E_ProgressBar--progress {
    background-color: var(--color-warn-over);
    box-shadow: 0px 0px 4px 1px var(--color-warn-over);
}

/*====================================================================*/
/*--------------------------- ResizableBox ---------------------------*/
/*====================================================================*/

.ResizableBox {
    position: relative;

    overflow: hidden;

    will-change: height, max-height, width, max-width;
}

.ResizableBox:not(.ResizableBox__floating) {
    min-height: 32px;
}

.ResizableBox:not(.ResizableBox__floating) .ResizableBox--self {
    display: flex;

    width: 100%;

    transition: var(--default-transition);
    transition-property: height;
}

.ResizableBox--scrollable {
    position: relative;
}

.ResizableBox:not(.ResizableBox__floating) .ResizableBox--scrollable {
    width: 100%;
}

/*---------------------------- CLOSE | MINMAX ----------------------------*/

.ResizableBox:not(.ResizableBox__floating) .ResizableBox--min-max,
.ResizableBox:not(.ResizableBox__floating) .ResizableBox--close {
    z-index: 1100;
    position: absolute;
    top: 0px;
    right: 0px;

    width: 24px;
    height: 24px;
    padding-top: 2px;

    display: flex;
    align-items: center;
    justify-content: center;

    -webkit-filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 1));
    -moz-filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 1));
    filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 1));

    transition: var(--default-transition);
}

.ResizableBox:not(.ResizableBox__floating) .ResizableBox--close {
    padding-right: 2px;
}

.ResizableBox:not(.ResizableBox__floating) .ResizableBox--min-max {
    padding-left: 4px;
}

.ResizableBox:not(.ResizableBox__floating) .ResizableBox--min-max svg,
.ResizableBox:not(.ResizableBox__floating) .ResizableBox--close svg {
    width: 12px;
    height: 12px;

    fill: #404040;
    opacity: 0.4;

    transition: var(--default-transition);
}

.ResizableBox:not(.ResizableBox__floating) .ResizableBox--min-max {
    right: 24px;
}

@media (pointer: coarse) {

    .ResizableBox:not(.ResizableBox__floating) .ResizableBox--min-max,
    .ResizableBox:not(.ResizableBox__floating) .ResizableBox--close {
        width: 30px;
        height: 30px;
        padding-top: 0;
    }

    .ResizableBox:not(.ResizableBox__floating) .ResizableBox--close {
        padding-right: 2px;
    }

    .ResizableBox:not(.ResizableBox__floating) .ResizableBox--min-max {
        padding-left: 8px;
    }

    .ResizableBox:not(.ResizableBox__floating) .ResizableBox--min-max svg,
    .ResizableBox:not(.ResizableBox__floating) .ResizableBox--close svg {
        width: 14px;
        height: 14px;
    }

    .ResizableBox:not(.ResizableBox__floating) .ResizableBox--min-max {
        right: 30px;
    }
}

.ResizableBox:not(.ResizableBox__floating) .ResizableBox--min-max__max {
    -webkit-filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.5));
    -moz-filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.5));
    filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.5));
}

.ResizableBox:not(.ResizableBox__floating) .ResizableBox--min-max__max svg {
    fill: var(--color-selected);
    opacity: 1;
}

.ResizableBox:not(.ResizableBox__floating) .ResizableBox--min-max:hover,
.ResizableBox:not(.ResizableBox__floating) .ResizableBox--close:hover {
    -webkit-filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.5));
    -moz-filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.5));
    filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.5));

    transition: var(--default-transition-hover);
}

.ResizableBox:not(.ResizableBox__floating) .ResizableBox--min-max:hover svg,
.ResizableBox:not(.ResizableBox__floating) .ResizableBox--close:hover svg {
    opacity: 1;

    transition: var(--default-transition-hover);
}

.ResizableBox:not(.ResizableBox__floating) .ResizableBox--min-max:active svg,
.ResizableBox:not(.ResizableBox__floating) .ResizableBox--close:active svg {
    transform: scale(0.9);

    transition: var(--default-transition-active);
}

/*---------------------------- RESIZER ----------------------------*/

.ResizableBox:not(.ResizableBox__floating) ~ .ResizableBox--resizer {
    z-index: 100;
    position: absolute;
    bottom: -7px;
    left: 0px;

    width: 100%;
    height: 12px;

    cursor: ns-resize;
}

@media (pointer: coarse) {

    .ResizableBox:not(.ResizableBox__floating) ~ .ResizableBox--resizer {
        bottom: -13px;

        height: 24px;
    }
}

.ResizableBox:not(.ResizableBox__floating) ~ .ResizableBox--resizer:after {
    content: "";

    position: absolute;
    top: calc(50% - 1px);
    left: 0px;

    width: 100%;
    height: 0px;

    border-bottom: 1px solid transparent;

    cursor: ns-resize;

    transition: var(--default-transition);

    transform: translateZ(0);
}

.ResizableBox:not(.ResizableBox__floating) ~ .ResizableBox--resizer:hover:after {
    border-bottom-color: var(--color-selected);

    transition: var(--default-transition-hover);
}

.ResizableBox:not(.ResizableBox__floating) ~ .ResizableBox--resizer__active:after,
.ResizableBox:not(.ResizableBox__floating) ~ .ResizableBox--resizer:active:after {
    border-bottom-color: var(--color-ok);

    transition: var(--default-transition-active);
}

/*====================================================================*/
/*------------------------------ BUTTON ------------------------------*/
/*====================================================================*/

.Button,
.Button:after,
.Button--content,
.Button--text,
.Button--icon svg,
.Button--icon {
    will-change: box-shadow, transform, background, fill, color;
    transition: var(--default-transition);
}

.Button:hover,
.Button:hover:after,
.Button:hover .Button--text,
.Button:hover .Button--icon svg,
.Button:hover .Button--icon,
.Button--event:focus ~ .Button--content,
.Button:focus,
.Button:focus:after,
