    touch-action: manipulation;
}

.ToggleField:after {
    content: "";

    z-index: 1;
    position: absolute;
    top: -1px;
    left: -1px;

    width: calc(100% + 2px);
    height: calc(100% + 2px);

    border-radius: inherit;
    box-shadow:
        inset 0px 0px 8px rgba(8, 161, 194, 0),
        inset 0px 0px 2px rgba(8, 161, 194, 0);

    will-change: box-shadow;
    transition: var(--default-transition);
    pointer-events: none;
}


